.ProseMirror blockquote {
  border-left: 3px solid rgba(255, 255, 255, 0.2);
  padding-left: 1em;
  margin: 1em 0;
  font-style: italic;
  color: rgba(255, 255, 255, 0.7);
}
.ProseMirror strong {
  font-weight: 600;
}
.ProseMirror em {
  font-style: italic;
}
.ProseMirror s {
  text-decoration: line-through;
}
.ProseMirror p.is-editor-empty:first-child::before {
  content: attr(data-placeholder);
  float: left;
  color: rgba(156, 163, 175, 0.6);
  pointer-events: none;
  height: 0;
}
.ProseMirror img.editor-image {
  max-width: 100%;
  height: auto;
  border-radius: 0.5rem;
  margin: 1em 0;
  display: block;
  cursor: pointer;
  border: 1px solid rgba(255, 255, 255, 0.1);
}
.ProseMirror img.editor-image:hover {
  border-color: rgba(255, 255, 255, 0.2);
}
.ProseMirror img.editor-image.ProseMirror-selectednode {
  outline: 2px solid rgba(59, 130, 246, 0.5);
  outline-offset: 2px;
}
.ProseMirror .mention {
  background-color: rgba(59, 130, 246, 0.2);
  border-radius: 0.25rem;
  padding: 0.125rem 0.25rem;
  color: rgb(147, 197, 253);
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
}
.ProseMirror .mention:hover {
  background-color: rgba(59, 130, 246, 0.3);
}
.ProseMirror .note-mention {
  background-color: rgba(34, 197, 94, 0.2);
  border-radius: 0.25rem;
  padding: 0.125rem 0.25rem;
  color: rgb(134, 239, 172);
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
}
.ProseMirror .note-mention:hover {
  background-color: rgba(34, 197, 94, 0.3);
}
.ProseMirror .file-mention {
  background-color: rgba(251, 146, 60, 0.2);
  border-radius: 0.25rem;
  padding: 0.125rem 0.25rem;
  color: rgb(253, 186, 116);
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
}
.ProseMirror .file-mention:hover {
  background-color: rgba(251, 146, 60, 0.3);
}
.ProseMirror .ai-completion-ghost-text {
  color: rgb(156, 163, 175);
  opacity: 0.5;
  pointer-events: none;
  user-select: none;
  font-style: italic;
}
.chat-input-editor .ProseMirror {
  min-height: 20px;
  max-height: 140px;
  overflow-y: auto;
}
.chat-input-editor .ProseMirror p {
  margin: 0;
}
.tiptap-message-content .ProseMirror, .tiptap-reply-preview .ProseMirror {
  padding: 0;
  min-height: auto;
}
.tiptap-message-content .ProseMirror p, .tiptap-reply-preview .ProseMirror p {
  margin: 0.25em 0;
}
.tiptap-message-content .ProseMirror p:first-child, .tiptap-reply-preview .ProseMirror p:first-child {
  margin-top: 0;
}
.tiptap-message-content .ProseMirror p:last-child, .tiptap-reply-preview .ProseMirror p:last-child {
  margin-bottom: 0;
}
.tiptap-reply-preview .ProseMirror {
  font-size: inherit;
  line-height: inherit;
}
.tiptap-renderer .ProseMirror {
  cursor: default;
}
.tiptap-renderer .ProseMirror * {
  cursor: inherit;
}
.tiptap-renderer .mention, .tiptap-renderer .note-mention {
  cursor: pointer;
}
:root {
  --background: #ffffff;
  --foreground: #171717;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
html,
body {
  background: var(--background);
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
  overscroll-behavior: none;
  -webkit-overflow-scrolling: auto;
}
* {
  overscroll-behavior: contain;
}
@keyframes bounce-x-left {
  0%,
  100% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(4px);
  }
  75% {
    transform: translateX(-2px);
  }
}
@keyframes bounce-x-right {
  0%,
  100% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-4px);
  }
  75% {
    transform: translateX(2px);
  }
}
.animate-bounce-x-left {
  animation: bounce-x-left 0.5s ease-in-out;
}
.animate-bounce-x-right {
  animation: bounce-x-right 0.5s ease-in-out;
}
